<template>
<div>
  <!-- pc端导航 -->
  <header>
      <div class="wrapper">
        <el-row>
          <el-col :span="3">
            <div class="logo threed">个人博客站</div>
          </el-col>
          <el-col :span="15">
            <!-- 导航菜单 -->
            <el-menu mode="horizontal"
            :default-active="activeIndex"
            @select="handleSelect"
            background-color="#2d2d2d"
            text-color="#9d9d9d"
            active-text-color="#fff"
            class="nav"
            >
              <el-menu-item index="1" @click="goToto">
                 <router-link to="/" ><i class="iconfont My-new-iconshouye"></i>博客首页</router-link>
              </el-menu-item>
              <el-menu-item index="2" @click="goToto">
                 <router-link to="/Lists"><i class="iconfont My-new-iconzixun"></i>文章分享</router-link>
              </el-menu-item>
              <el-menu-item index="3" @click="goToto">
                  <router-link to="/timeLocus" ><i class="iconfont My-new-icondaojishi"></i>归档</router-link>
              </el-menu-item>
              <el-menu-item index="5" @click="goToto">
                 <router-link to="/aboutMe" ><i class="iconfont My-new-iconwode1"></i>关于我</router-link>
              </el-menu-item>
              <el-menu-item index="6" @click="goToto">
                 <router-link to="/LeaveWord"><i class="iconfont My-new-iconbianji"></i>留言</router-link>
              </el-menu-item>
              <el-menu-item index="9" @click="goToto">
                 <router-link to="/login"  v-if="isSignIn === 0" class="login"><i class="iconfont My-new-iconxuanzhonghaoyou"></i>登录</router-link>
              </el-menu-item>
              <el-menu-item index="10">
                <el-dropdown class="menuAvatarBox" placement="bottom">
                  <span class="el-dropdown-link">
                    <router-link to="/personal"  v-if="isSignIn === 1 "  class="login">
                     {{UserInfo.type === true ? '尊敬的管理员：' + UserInfo.nickname : '用户：'+ UserInfo.nickname}},欢迎您
                   </router-link>
                    <el-icon class="el-icon--right">
                      <arrow-down />
                    </el-icon>
                  </span>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item @click.native="menuQuit">退出</el-dropdown-item>
                      <el-dropdown-item @click.native="goToUserInfo">个人中心</el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
              </el-menu-item>
            </el-menu>
          </el-col>
          <!--搜索 -->
          <el-col :span="6">
            <Search/>
          </el-col>
        </el-row>
      </div>
  </header>
  <!-- 移动端导航 -->
  <div id="mobileNav">
    <el-row class="tac">
      <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          :router="true">
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span class="logotit">个人博客站</span>
              <span v-if="UserInfo.nickname">{{UserInfo.nickname}},欢迎您</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="/" @click="Goto"><i class="iconfont My-new-iconshouye"></i>博客首页</el-menu-item>
              <el-menu-item index="/Lists" @click="Goto"><i class="iconfont My-new-iconzixun"></i>文章分享</el-menu-item>
              <el-menu-item index="/timeLocus" @click="Goto"><i class="iconfont My-new-icondaojishi"></i>归档</el-menu-item>
              <el-menu-item index="/aboutMe" @click="Goto"><i class="iconfont My-new-iconwode1"></i>关于我</el-menu-item>
              <el-menu-item  index="/LeaveWord" @click="Goto"><i class="iconfont My-new-iconbianji"></i>留言</el-menu-item>
              <el-menu-item index="/personal" v-if="UserInfo.nickname" @click="goToUserInfo"><i class="iconfont My-new-iconwode1"></i>{{UserInfo.nickname}}的个人中心</el-menu-item>
              <el-menu-item  index="/login" v-else @click="Goto">登录</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
    </el-row>
  </div>

</div>

</template>

<script>
import Search from './search/index.vue'
  export default {
    data() {
      return {
        activeIndex:'1',//当前激活菜单
        UserInfo:{},
        isSignIn:0,//用户是否登录
      }
    },
    components:{
      Search//搜索组件
    },
    methods: {
      // 修改当前菜单活跃
        handleSelect(index) {
          this.activeIndex = index + ''
        },
      goToto(){
        this.$emit('goTo')
      },
      //获取用户信息
      loadUserInfo(){
        const user=JSON.parse(sessionStorage.getItem("user"))
        if(user){
          this.UserInfo=user
          this.isSignIn=1
        }
      },
      // 移动端导航
      handleOpen(key, keyPath,index) {
        console.log(key, keyPath);
        console.log(index);
      },
      handleClose(key, keyPath,index) {
        console.log(key, keyPath);
        console.log(index);
      },
      Goto(){
        let but = document.querySelector('.el-submenu__title');
        but.click();
      },
      //退出方法
      menuQuit(){
        //清除浏览器sessionStorage的用户信息
        sessionStorage.removeItem("user")
        // 退出成功，去vuex里修改登录状态
        this.$store.commit('changIsSignIn',0)
        this.isSignIn=0
      },
      //进入用户个人信息
      goToUserInfo(){
        this.$router.push({name:'UserInfo'})
      },
    },
    created(){
      this.loadUserInfo()
    },
    mounted() {
      this.loadUserInfo()
    }

  }
</script>

