<!-- 文章标签标签 -->
<template>
  <div>
    <div class="block" id="tally">
      <h4 style="margin-bottom:15px">文章标签</h4>
        <el-tag v-show="(item.name)" :key="index" v-for="(item,index) in AllArticleTagName" @click="GotoList(item.id)">{{item.name}}</el-tag>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        //文章标签数据
         AllArticleTagName:[
           {
             id:'',//分类id
             name: "数据库",//分类名称
             blogQuantity: 1,//文章数
           }
         ],
      }
    },
    methods:{
          // 获取所有文章标签
      GetAllArticleClassName(){
        this.$http.get('/tag/selectTagAndBlogQuantity').then(res => {
          this.AllArticleTagName = res.data.data.tagAndBlogQuantity
        })
      },
      // 点击标签去列表
      GotoList(tag){
        this.$router.push({name:'Lists',query: {tag}})
      }
    },
    created(){
      this.GetAllArticleClassName()
    }
  }
</script>

<style lang="scss" scoped>
.el-tag {
  margin: 6px;
  cursor:pointer;
}
</style>
