<template>
  <div class="commonLayout">
    <!-- 导航条 -->
    <nav>
      <common-header @goTo="goTo()" class="wow slideInRight common-header" style="top:0px"></common-header>
    </nav>

    <!-- 切换大屏阅读模式按钮 -->
    <check-box isname="read"></check-box>

      <!-- 中心 -->
      <section>
        <div class="wrapper marginTop">
            <el-row :gutter="10" class="head" v-show="!isShowDiv">
              <el-col :xs="24" :sm="24" :md="24" :lg="18" :xl="18">
                <div class="grid-content bg-purple slideshowBox">
                  <!-- 视图切换 -->
                    <article ref="article">
                      <keep-alive include="timeLocus">
                        <router-view></router-view>
                      </keep-alive>
                    </article>
                </div>
                </el-col>
              <el-col :lg="6" :xl="6">
                <div class="grid-content bg-purple-light synopsis" ref="sidebar">
                  <!-- 侧边栏 -->
                    <aside class="wow slideInRight">
                      <!-- 文章分类组件 -->
                      <tally class="maginbot"></tally>
                      <!-- 文章标签组件 -->
                      <tag class="maginbot"></tag>
                      <!--最新推荐-->
                      <div class="block maginbot">
                        <title-boxs title="最新推荐"></title-boxs>
                        <div class="recommendItem"
                             v-for="item in recommendVoList"
                             @click="enterArticle(item.id)"
                        >
                          {{ item.title }}
                        </div>
                      </div>
                    </aside>
                </div>
                </el-col>
            </el-row>

        </div>
      </section>
    <!-- 底部 -->
    <footer>
      <common-bottom></common-bottom>
    </footer>
  </div>
</template>

<script>
import CommonHeader from '@/components/CommonHeader.vue'
import CommonBottom from './CommonBottom.vue'
import Tally from './sidebar/tally.vue'
import Tag from './sidebar/tag'
import TitleBoxs from '@/components/TitleBox/titleBoxs.vue'
import CheckBox from '@/components/checkbox/checkbox.vue'

export default {
  name: 'commonLayout',
  components:{
    CommonHeader,//导航组件
    CommonBottom,
    Tally,//文章分类组件
    Tag,//文章标签组件
    TitleBoxs,
    CheckBox,
  },
  data() {
    return {
      isShowDiv:false,
      recommendVoList:[
        {
          id: 70,//文章id
          title: "注解和反射",//文章标题
        }
      ],//最新推荐文章列表
    }
  },
  methods: {
    goTo(){
      this.isShowDiv = false
    },
    //获取最新推荐文章
    logRecommend(){
      this.$http.get('/blog/selectRecommend').then(res => {
        this.recommendVoList=res.data.data.recommendVoList
      })
    },
    //推荐文章详情
    enterArticle(id){
      this.$router.push({name:'detail',params: {id:id}})
    },

  },
  created() {
    this.logRecommend()
  }

}
</script>

