<!-- 文章分类标签 -->
<template>
  <div>
    <div class="block" id="tally">
      <h4 style="margin-bottom:15px">文章分类</h4>
        <el-tag v-show="(item.name)" :key="index" v-for="(item,index) in AllArticleClassName" @click="GotoList(item.id)">{{item.name}}</el-tag>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
         AllArticleClassName:[
           {
             id:'',//分类id
             name: "数据库",//分类名称
             blogQuantity: 1,//文章数
           }
         ],
      }
    },
    methods:{
          // 获取所有文章分类
      GetAllArticleClassName(){
        this.$http.get('/type/selectTypeAndBlogQuantity').then(res => {
          this.AllArticleClassName = res.data.data.typeAndBlogQuantity
        })
      },
      // 点击标签去列表
      GotoList(key){
        this.$router.push({name:'Lists',query: {key}})
      }
    },
    created(){
      this.GetAllArticleClassName()
    }
  }
</script>

<style lang="scss" scoped>
.el-tag {
  margin: 6px;
  cursor:pointer;
}
</style>
