<!--搜索框组件-->
<template>
  <div class="search">
    <input maxlength="16" @focus="onFocus" @blur="onBlur" v-model="inputVal" autocomplete="off" placeholder="请输入关键字..." name="s" class="input" type="text">
    <button type="submit" class="submit" @click="SearchWord">搜索</button>
    <transition name="el-fade-in-linear">
        <div v-show="show" class="transition-box">
          <SearchItem
            :key="index"
            v-for="(item,index) in dataArr"
            :id="item.id"
            :index="index"
            :title="item.title"
            :like_count="item.views"
            @click.native="handle(item.title)"
          />
        </div>
    </transition>
  </div>
</template>

<script>
import SearchItem from  './searchItem.vue'
  export default {
    data(){
      return {
        inputVal:null,//搜索条件
        show: false,//搜索框下拉展示状态
        //搜索下拉框展示内容
        dataArr:[
          {
            id: '1',//文章id
            views: '15',//文章浏览量
            title: "@ControllerAdvice 注解的使用场景",//文章标题
          }
        ]
      }
    },
    components:{
      SearchItem
    },
    methods:{
      //点击搜索按钮
      SearchWord(){
        const title=this.inputVal
        this.$router.push({name:'Lists',query: {title}})
      },
      //输入框获取焦点
      onFocus(){
        this.show = !this.show
      },
      //输入框失去取焦点
      onBlur(){
        this.show = !this.show
      },
      //点击下拉选项方法
      handle(title){
        this.$router.push({name:'Lists',query: {title}})
      },
      //根据文章浏览量进行排序
      handleDataType(arr){
        for(let i = 0; i < arr.length - 1; i++){
          for(let j = 0; j < arr.length - i - 1; j++){
              let temp = null;
            if(arr[j].views > arr[j + 1].views){
                temp = arr[j];
                arr[j] = arr[j + 1];
                arr[j + 1] = temp;
            }
          }
        }
        return arr.reverse()
      },
      //获取最新发布的6篇文章
      async getTypeList (){
       let res  = await this.$http.get('/blog/selectNewBlog');
       this.dataArr = this.handleDataType(res.data.data.newBlog);
      }
    },
    created(){
      this.getTypeList()
    }
  }
</script>

